import React from 'react';
import ReactDOM from 'react-dom';
import './index.css';  // Si tienes estilos globales
import App from './App'; // El componente principal de tu aplicacin

// Renderiza el componente App en el contenedor con id="root"
ReactDOM.render(
  <React.StrictMode>
    <App />
  </React.StrictMode>,
  document.getElementById('root')
);