import React, { useState, useEffect } from 'react';
import axios from 'axios';

function AdminPanel() {
  const [platforms, setPlatforms] = useState([]);

  useEffect(() => {
    // Hacer la solicitud GET al backend para obtener las plataformas
    axios.get('/api/plataformas') // Este endpoint debera devolver los datos de las plataformas
      .then(response => {
        setPlatforms(response.data); // Almacena los datos en el estado
      })
      .catch(error => {
        console.error('Error fetching platforms:', error);
      });
  }, []); // El array vaco asegura que solo se haga una vez al montar el componente

  return (
    <div>
      <h1>Panel de Administracin</h1>
      <h2>Plataformas</h2>
      <ul>
        {platforms.length > 0 ? (
          platforms.map(platform => (
            <li key={platform._id}>{platform.nombre}</li> // Muestra el nombre de cada plataforma
          ))
        ) : (
          <p>No hay plataformas disponibles</p> // Mensaje si no hay datos
        )}
      </ul>
    </div>
  );
}

export default AdminPanel;

